import utils from '~/common/utils'
// import TagInput from '~/components/TagInput'
// import MarkdownHelp from '~/components/MarkdownHelp'
const Geohash = require('@geonet/geohash')

class GeoUtil {
  convertLatlonToGeohash(latlon) {
    return Geohash.encode(latlon[0], latlon[1], 11)
  }

  getLatlonFromGeoRequest(geoObject) {
    let geohashStr = ''
    let latlonStr = ''
    let district = ''
    if (geoObject.geocodes.length > 0) {
      const geocode = geoObject.geocodes[0]

      // console.log('geocode:' + JSON.stringify(geocode))
      if (geocode.location !== undefined) {
        district = geocode.district
        const latlon = geocode.location.split(',')
        geohashStr = this.convertLatlonToGeohash(latlon)
        latlonStr = geocode.location

        // resultList.push({
        //   name: address,
        //   latlon: lotlon_str,
        //   geohash: geohash_str
        // })
        // console.log('result:' + geohashStr)
      }
    }
    return {
      geohash: geohashStr,
      latlon: latlonStr,
      district
    }
  }

  requestAmapGeo(axios, address, callback) {
    const amapapi =
      '/v3/geocode/geo?key=06a39860cdf741c5dc5ba1242112ab72&city=' +
      utils.getCity() +
      '&address=' +
      address

    axios
      .get(amapapi)
      .then(function(response) {
        // console.log('result:' + response)
      })
      .catch(function(reponse) {
        if (callback) {
          callback(reponse)
        }
      })
  }

  hasRole(user, role) {
    if (!user || !user.roles || !user.roles.length) {
      return false
    }
    for (let i = 0; i < user.roles.length; i++) {
      if (user.roles[i] === role) {
        return true
      }
    }
    return false
  }

  hasAnyRole(user, ...roles) {
    if (!roles || !roles.length) {
      return false
    }
    for (let i = 0; i < roles.length; i++) {
      if (this.hasRole(user, roles[i])) {
        return true
      }
    }
    return false
  }

  isOwner(user) {
    return this.hasRole(user, 'owner')
  }

  isAdmin(user) {
    return this.hasRole(user, 'admin')
  }
}

export default new GeoUtil()
