import VueCascaderSelect from 'vue-cascader-select'
import jsonConventer from '~/common/jsonConventer'
import utils from '~/common/utils'
import GeoUtil from '~/common/GeoUtil'
// import TagInput from '~/components/TagInput'
// import MarkdownHelp from '~/components/MarkdownHelp'
import PostTweets from '~/components/PostTweets'
import StorageUtil from '~/common/StorageUtil'
// import MarkdownEditor from '~/components/MarkdownEditor'

export default {
  middleware: 'authenticated',
  components: {
    VueCascaderSelect,
    // TagInput,
    PostTweets
    // MarkdownHelp
  },
  async asyncData({ $axios, query, store }) {
    // // 节点
    // const nodes = await $axios.get('/api/topic/nodes')
    // // 发帖标签
    // const config = store.state.config.config || {}
    // const nodeId = query.nodeId || config.defaultNodeId
    // let currentNode = null
    // if (nodeId) {
    //   try {
    //     currentNode = await $axios.get('/api/topic/node?nodeId=' + nodeId)
    //   } catch (e) {
    //     console.error(e)
    //   }
    // }
    // return {
    //   nodes,
    //   postForm: {
    //     nodeId: currentNode ? currentNode.nodeId : 0
    //   }
    // }
  },
  data() {
    return {
      publishing: false, // 当前是否正处于发布中...
      captchaId: '',
      captchaUrl: '',
      captchaCode: '',
      imageList: [],
      postForm: {
        nodeId: 2,
        title: '',
        tags: [],
        content: '',
        address: ''
      },
      subwaysAndRegions: [],
      value: '',
      geoStationId: -1, // 地铁站对应的id
      options: [
        {
          label: 'Frontend',
          disabled: true,
          // selectable: true,
          options: [
            { label: 'Vue', value: 'Vue' },
            { label: 'React', value: 'React' },
            { label: 'Svelte', value: 'Svelte' }
          ]
        },
        {
          label: 'Backend',
          value: 'Backend',
          disabled: true,
          // selectable: true,
          options: [
            { label: 'Ruby on Rails', value: 'Ruby on Rails' },
            { label: 'NodeJS', value: 'NodeJS' },
            { label: 'Elixir', value: 'Elixir' }
          ]
        }
      ]
    }
  },
  computed: {
    user() {
      return this.$store.state.user.current
    },
    config() {
      return this.$store.state.config.config
    }
  },
  mounted() {
    this.showCaptcha()
    this.checkConfig()
  },
  methods: {
    async checkConfig() {
      const city = utils.getCity()
      const subways = StorageUtil.getSubwayAndRegion(city)
      // console.log('subways get :' + JSON.stringify(subways))
      if (!subways) {
        const [subwaysRegions] = await Promise.all([
          this.$axios.get('/api/topic/subway', {
            params: {
              city
            }
          })
        ])
        // console.log(
        //   'subwaysAndRegions get remote:' + JSON.stringify(subwaysRegions)
        // )
        this.subwaysAndRegions = subwaysRegions
        if (subwaysRegions && subwaysRegions.length > 0) {
          StorageUtil.setSubwayAndRegion(city, subwaysRegions)
        }
      } else {
        // console.log('subwaysAndRegions get cache:' + JSON.stringify(subways))
        this.subwaysAndRegions = subways
      }

      this.checkSubwayStationData()
      this.formatSubwaySelectData()
    },
    showMetraSelectDiv() {
      return utils.getCity() === '北京' || utils.getCity() === '武汉'
    },
    onImageUpload(imageList) {
      this.imageList = imageList
    },
    checkRentAddress(succCallback, errorCallback) {
      const address = this.postForm.address
      if (address === '') {
        return
      }

      GeoUtil.requestAmapGeo(
        this.$axios,
        this.postForm.address,
        (geoObject) => {
          const result = GeoUtil.getLatlonFromGeoRequest(geoObject)
          // console.log('requestAmapGeo:' + JSON.stringify(result))
          if (result.geohash === '' || result.latlon === '') {
            if (errorCallback) {
              errorCallback()
            }
          } else if (succCallback) {
            succCallback(result.latlon, result.geohash, result.district)
          }
        }
      )
    },
    async submitTopic(latlon, geoHash, district) {
      try {
        const tags = this.value !== '' ? [this.value] : []
        const transferContent = jsonConventer.homeItemToJsonString(
          '北京',
          this.postForm.address,
          tags.join(',')
        )

        const topic = await this.$axios.post('/api/topic/rent/create', {
          city: utils.getCity(),
          captchaId: this.captchaId,
          captchaCode: this.captchaCode,
          nodeId: this.postForm.nodeId,
          title: this.postForm.title,
          content: this.postForm.content,
          tags: tags.join(','),
          imageList: this.imageList.toString(),
          extraData: JSON.stringify(transferContent),
          latlon,
          geoHash,
          district,
          geoStationId: this.geoStationId
        })
        // this.$refs.mdEditor.clearCache()
        this.$toast.success(
          '提交成功！ 您可以进入详情页，点击分享按钮，实现一键分享入微信群',
          {
            duration: 2000,
            onComplete() {
              utils.linkTo('/rent/' + topic.topicId)
            }
          }
        )
      } catch (e) {
        console.log(e)
        await this.showCaptcha()
        this.publishing = false
        this.$toast.error('提交失败：' + (e.message || e))
      }
    },
    submitCreate() {
      if (this.publishing) {
        this.$toast.error('正在提交中')
        return
      }

      if (!this.postForm.title) {
        this.$toast.error('请输入标题')
        return
      }

      if (!this.postForm.address) {
        this.$toast.error('请输入地点')
        return
      }

      this.publishing = true
      this.checkRentAddress(
        (latlon, geohash, district) => {
          let distrctName = ''
          if (district.includes('区') && district.length > 2) {
            distrctName = district.substring(0, district.length - 1)
          } else {
            distrctName = district
          }

          // console.log(
          //   'checkRentAddress success:' +
          //     latlon +
          //     ' ' +
          //     geohash +
          //     ' ' +
          //     distrctName
          // )
          this.submitTopic(latlon, geohash, distrctName)
        },
        () => {
          this.publishing = false
          this.$toast.error(
            '提交失败，请输入有效地址。例如：[西二旗地铁站] 或者 [兰园小区]'
          )
          console.log('checkRentAddress error:' + this.postForm.address)
        }
      )
    },
    checkSubwayStationData() {
      const city = utils.getCity()
      const subways = StorageUtil.getSubwayAndRegion(city)
      // console.log('checkSubwayStationData:' + JSON.stringify(subways))
      if (subways) {
        const subwayDtype = 0
        const ids = []
        subways.forEach((item) => {
          // 地铁
          if (item.dtype === subwayDtype) {
            const res = StorageUtil.getStationList(
              city,
              subwayDtype,
              item.dataId
            )
            if (res) {
            } else {
              ids.push(item.dataId)
            }
          }
        })

        if (ids.length > 0) {
          this.requestStationList(subwayDtype, ids, city)
        }
      }
    },
    requestStationList(dtype, ids, city) {
      const params = {
        dtype,
        dataIds: ids.toString(),
        city
      }

      this.$axios
        .get('/api/topic/stationlist2', { params })
        .then((res) => {
          if (res && res.length > 0) {
            // console.log('requestStationList:' + JSON.stringify(res))
            res.forEach((item) => {
              if (item.stationlist) {
                StorageUtil.setStationList(
                  city,
                  dtype,
                  item.dataId,
                  item.stationlist
                )
              }
            })
            // StorageUtil.setStationList(city, dtype, dataId, res)
            this.formatSubwaySelectData()
          }
        })
        .catch((e) => {
          console.log(
            'create page, requestStationList error:' + JSON.stringify(e)
          )
        })
    },
    async showCaptcha() {
      if (this.config.topicCaptcha) {
        try {
          const ret = await this.$axios.get('/api/captcha/request', {
            params: {
              captchaId: this.captchaId || ''
            }
          })
          this.captchaId = ret.captchaId
          this.captchaUrl = ret.captchaUrl
        } catch (e) {
          this.$toast.error(e.message || e)
        }
      }
    },
    onClearSubway(value) {
      this.value = ''
      this.geoStationId = -1
    },
    onSelectSubway(value) {
      // console.log('select:' + JSON.stringify(value))
      if (value.value) {
        const arr = value.value.split('-')
        if (arr.length === 3) {
          this.geoStationId = arr[0]
          this.value = arr[1] + '-' + arr[2]
        }
      }
    },
    formatSubwaySelectData() {
      const result = []
      const city = utils.getCity()

      const arr = this.subwaysAndRegions
      if (arr) {
        arr.forEach((item) => {
          if (item.dtype === 0) {
            const options = []
            const subwayStations = StorageUtil.getStationList(
              city,
              0,
              item.dataId
            )
            if (subwayStations) {
              subwayStations.forEach((station) => {
                options.push({
                  label: station.name,
                  value:
                    station.geoStationId + '-' + item.name + '-' + station.name
                })
              })
              result.push({
                label: item.name,
                value: item.name,
                disabled: true,
                options
              })
            }
          }
        })
      }

      // console.log('formatSubwaySelectData :' + JSON.stringify(result))

      this.options = result
    },
    tweetsCreated(item) {
      this.$refs.tweetsLoadMore.unshiftResults(item)
    }
  },
  head() {
    return {
      title: this.$siteTitle('租房')
    }
  }
}
